import React, { Component } from 'react';
import CartTextarea from '../cart-textarea/CartTextarea';
import CartButton from '../cart-button/CartButton';

class CartDelivery extends Component {
  changeAddress = e => {
    const { onChange } = this.props;
    onChange('address', e.target.value);
  };
  changeComment = e => {
    const { onChange } = this.props;
    onChange('comment', e.target.value);
  };
  render() {
    const { withPayment, goToNextTab, data, showWarning } = this.props;
    return (
      <div className="cart-delivery">
        <form className="cart-delivery__form">
          <CartTextarea
            showWarning={showWarning && !data.address}
            value={data.address}
            onChange={this.changeAddress}
            id="address"
            rows={3}
            placeholder="Адрес доставки"
          />
          <CartTextarea
            value={data.comment}
            onChange={this.changeComment}
            id="comment"
            rows={5}
            placeholder="Комментарий"
          />
        </form>
        {withPayment && (
          <div className="cart-order__control">
            <CartButton onClick={goToNextTab} type="simple" value="Далее" />
          </div>
        )}
      </div>
    );
  }
}

export default CartDelivery;
