import React, { Component } from 'react';
import CartInput from '../cart-input/CartInput';

class CartPayment extends Component {
  changeNumber = e => {
    const { onChange } = this.props;
    onChange('number', e.target.value);
  };
  changeOwner = e => {
    const { onChange } = this.props;
    onChange('owner', e.target.value);
  };
  changeDate = e => {
    const { onChange } = this.props;
    onChange('date', e.target.value);
  };
  changeCvc = e => {
    const { onChange } = this.props;
    onChange('cvc', e.target.value);
  };
  render() {
    const { data, showWarning } = this.props;
    return (
      <div className="cart-payment">
        <form className="cart-payment__form">
          <CartInput
            showWarning={showWarning && (!data.number || data.number.length < 19)}
            key="card"
            mask="9999 9999 9999 9999"
            placeholder="Номер карты"
            type="text"
            disableMaskChar
            onChange={this.changeNumber}
            value={data.number}
          />
          <CartInput
            showWarning={showWarning && !data.owner}
            value={data.owner}
            key="owner"
            placeholder="Владелец карты"
            type="text"
            onChange={this.changeOwner}
            isUppercase
          />
          <CartInput
            showWarning={showWarning && (!data.date || data.date.length < 5)}
            key="date"
            mask="99/99"
            placeholder="Срок действия"
            type="text"
            disableMaskChar
            value={data.date}
            onChange={this.changeDate}
          />
          <CartInput
            showWarning={showWarning && (!data.cvc || data.cvc.length < 3)}
            value={data.cvc}
            key="cvc"
            onChange={this.changeCvc}
            mask="999"
            disableMaskChar
            placeholder="CVC"
            type="password"
          />
        </form>
      </div>
    );
  }
}

export default CartPayment;
